.ag-chart {
    width: 100%;
    height: 100%
}

.ag-chart,.ag-chart-components-wrapper {
    position: relative;
    display: flex;
    overflow: hidden
}

.ag-chart-components-wrapper {
    flex: 1 1 auto
}

.ag-chart-title-edit {
    position: absolute;
    display: none;
    top: 0;
    left: 0;
    text-align: center
}

.ag-chart-title-edit.currently-editing {
    display: inline-block
}

.ag-chart-canvas-wrapper {
    position: relative;
    flex: 1 1 auto;
    overflow: hidden
}

.ag-charts-canvas {
    display: block
}

.ag-chart-menu {
    position: absolute;
    top: 10px;
    width: 24px;
    overflow: hidden;
    display: flex;
    flex-direction: column
}

.ag-ltr .ag-chart-menu {
    right: 20px
}

.ag-rtl .ag-chart-menu {
    left: 20px
}

.ag-chart-docked-container {
    position: relative;
    width: 0;
    min-width: 0;
    transition: min-width .4s
}

.ag-chart-menu-hidden~.ag-chart-docked-container {
    max-width: 0;
    overflow: hidden
}

